.tableContainer{letter-spacing:0;display:flex;align-items:flex-start;flex-direction:column;border:1px solid #656565}.table{font-size:0.8em;width:100%;background:#e0e0e0;table-layout:fixed;border-collapse:collapse}.table tr{height:3em}.tableHead{border-bottom:1px solid #9b9b9b}.tableBody tr:nth-of-type(even){background:#f5f5f5}.category{cursor:pointer;font-size:1.1em}.icon{opacity:0;transition:0.66s}.iconActivated{opacity:1;composes:icon}.activated{composes:category}.activated .icon{color:#144664}thead{background:#f5f5f5}tbody,thead{display:block}tbody{height:420px;overflow-y:scroll;-webkit-overflow-scrolling:touch}th,td{width:25%}



